.clients
    background-color: $light-gray-color

    &__container

    &__title
        text-align: center
        margin-bottom: 60px

    &__items
        display: grid
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr))
        gap: 50px

    &__item
        display: flex
        align-items: center
        justify-content: center

    &__link
        display: block
        img
            width: 100%
