* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid #fc2db7;
  outline-offset: 4px;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "BebasNeue", sans-serif;
  color: #000;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 1.2px;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "BebasNeue", sans-serif;
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  outline: none;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}
.main__home {
  padding: 200px 0 30px 0;
  min-height: 600px;
}
.main__benefits {
  padding: 70px 0;
}
.main__popular {
  padding: 70px 0 50px 0;
}
.main__partners {
  padding: 70px 0;
}
.main__new {
  padding: 70px 0 50px 0;
}
.main__categories {
  padding: 70px 0;
}
.main__clients {
  padding: 70px 0;
}
.main__catalog {
  padding: 200px 0 30px 0;
}

.container {
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  background-color: #654f5e;
  font-weight: 500;
  border-radius: 20px;
  transition: box-shadow 0.3s ease, background-color 0.3s ease;
}
@media (any-hover: hover) {
  .btn:hover {
    box-shadow: 0 0 3px 3px rgba(194, 63, 119, 0.4);
    background-color: #fc2db7;
  }
}

.title {
  position: relative;
  font-weight: 700;
  font-size: 42px;
  color: #654f5e;
}
.title span {
  color: #fc2db7;
}
.title::after {
  content: "";
  position: absolute;
  width: 15%;
  height: 10px;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fc2db7;
}

.product-item {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 10px 5px rgba(194, 63, 119, 0.4);
}
.product-item__image {
  padding: 5px;
  width: 100%;
  margin-bottom: 10px;
}
.product-item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.product-item__content {
  padding: 10px;
  color: #654f5e;
}
.product-item__title {
  display: block;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  color: #654f5e;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .product-item__title:hover {
    color: #fc2db7;
  }
}
.product-item__prices {
  margin-bottom: 10px;
}
.product-item__price {
  margin-right: 10px;
  font-weight: 700;
}
.product-item__price_old {
  font-weight: 500;
  text-decoration: line-through;
}
.product-item__bottom {
  display: grid;
  grid-template-columns: 1.5fr 1.5fr 3fr;
  gap: 10px;
  width: 100%;
}

.item-bottom__label {
  display: block;
  width: 100%;
  border: 1px solid #654f5e;
}
.item-bottom__input {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100%;
}
.item-bottom__btns {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.item-bottom__btn {
  border-radius: 10px;
}
.item-bottom__btn_plus {
  margin-bottom: 5px;
}
.item-bottom__button {
  border-radius: 10px;
  padding: 5px;
}

.checkbox {
  appearance: none;
}
.checkbox:checked + .checkbox-tag::after {
  transform: scale(1);
}
.checkbox:focus-visible + .checkbox-tag {
  outline: 2px solid #654f5e;
  outline-offset: 3px;
}
.checkbox input::-webkit-outer-spin-button,
.checkbox input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox-tag {
  position: relative;
  display: inline-block;
}
.checkbox-tag::before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: 15px;
  height: 15px;
  border: 2px solid #654f5e;
}
.checkbox-tag::after {
  transition: all 0.3 ease 0s;
  position: absolute;
  content: "";
  top: 1px;
  left: 3px;
  width: 9px;
  height: 9px;
  background-color: #654f5e;
  transform: scale(0);
}

