.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 500;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  background-color: #ebedee;
}
.header.active {
  background-color: rgba(238, 238, 238, 0.95);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);
}
.header__body {
  min-height: 150px;
  display: flex;
  align-items: center;
}
.header__logo {
  display: block;
  flex: 0 0 160px;
  background-color: #fc2db7;
  border-radius: 10px;
  border: 7px solid #ffffff;
  margin-right: 40px;
  color: #ffffff;
  font-size: 36px;
  font-weight: 700;
  letter-spacing: 1px;
  padding: 10px;
  text-align: center;
}
.header__logo span {
  display: block;
  font-weight: 400;
  margin-top: -5px;
  font-size: 30px;
  letter-spacing: 3px;
}
.header__content {
  flex: 1 1 auto;
}
.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
}
.header__main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 0;
}
.header__bottom {
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.top-header__phones {
  display: flex;
  align-items: center;
}
.top-header__phone {
  display: flex;
  align-items: center;
  color: #654f5e;
  font-size: 14px;
  font-weight: 700;
  transition: color 0.3s ease;
}
.top-header__phone:not(:last-child) {
  margin-right: 25px;
}
.top-header__phone:before {
  font-size: 16px;
  margin-right: 5px;
}
@media (any-hover: hover) {
  .top-header__phone:hover {
    color: #fc2db7;
  }
}

.search-header__body {
  display: flex;
  align-items: center;
  height: 40px;
  width: 400px;
  background-color: #ffffff;
  border-radius: 20px;
  padding: 2px;
  transition: box-shadow 0.3s ease;
}
.search-header__body:focus-within {
  box-shadow: 0 0 2px 2px rgba(252, 45, 183, 0.4);
}
.search-header__label {
  padding: 2px 10px;
  width: 100%;
  height: 100%;
}
.search-header__input {
  width: 100%;
  height: 100%;
  font-size: 14px;
  color: #654f5e;
  outline: none;
  padding-left: 5px;
  transition: box-shadow 0.3s ease;
}
.search-header__btn {
  flex: 0 0 36px;
  height: 36px;
  padding: 5px;
  font-size: 16px;
  border-radius: 100%;
  background-color: #654f5e;
}

.bottom-header__list {
  display: flex;
  align-items: center;
}
.bottom-header__item:not(:last-child) {
  margin-right: 30px;
}
.bottom-header__link {
  color: #654f5e;
  position: relative;
  font-weight: 700;
}
.bottom-header__link::before, .bottom-header__link::after {
  content: "";
  position: absolute;
  bottom: -5px;
  width: 0;
  height: 2px;
  opacity: 0;
  background-color: #fc2db7;
  transition: all 0.3s ease 0s;
}
.bottom-header__link::before {
  left: 50%;
}
.bottom-header__link::after {
  right: 50%;
}
@media (any-hover: hover) {
  .bottom-header__link:hover {
    color: #fc2db7;
  }
  .bottom-header__link:hover::before, .bottom-header__link:hover::after {
    width: 50%;
    opacity: 1;
  }
}
.bottom-header__actions {
  display: flex;
  align-items: center;
}
.bottom-header__login, .bottom-header__registration, .bottom-header__cart {
  font-size: 14px;
  margin-right: 20px;
  font-weight: 700;
  color: #654f5e;
  transition: color 0.3s ease;
}
.bottom-header__login:before, .bottom-header__registration:before, .bottom-header__cart:before {
  margin-bottom: 5px;
  font-size: 22px;
}
.bottom-header__login span, .bottom-header__registration span, .bottom-header__cart span {
  display: inline-block;
  font-weight: 700;
}
@media (any-hover: hover) {
  .bottom-header__login:hover, .bottom-header__registration:hover, .bottom-header__cart:hover {
    color: #fc2db7;
  }
}
.bottom-header__burger {
  display: none;
}
.bottom-header__callback {
  height: 40px;
  padding: 5px 15px;
  font-size: 14px;
  background-color: #fc2db7;
}
.bottom-header__callback::before {
  margin-right: 5px;
  font-size: 26px;
}

.home {
  background-color: #ebedee;
}
.home__slider {
  position: relative;
  padding: 0 40px;
}

.slider-home__body {
  height: 400px;
  width: 100%;
}
.slider-home__slide {
  width: 100%;
  height: 100%;
  position: relative;
}
.slider-home__arrow {
  position: absolute;
  top: 40%;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  z-index: 10;
  background-color: #654f5e;
}
.slider-home__arrow_prev {
  left: -10px;
}
.slider-home__arrow_next {
  right: -10px;
}
.slider-home__dotts {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.slide-home__image {
  width: 100%;
  height: 100%;
}
.slide-home__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.slide-home__content {
  position: absolute;
  bottom: 20px;
  left: 20px;
  width: 45%;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.8);
}
.slide-home__title {
  color: #654f5e;
  font-size: 32px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 15px;
}
.slide-home__descr {
  color: #654f5e;
  font-weight: 500;
  margin-bottom: 20px;
  font-family: "PFDinTextCompPro";
