  }
}

.filter-catalog__title {
  font-weight: 700;
  font-size: 42px;
  color: #654f5e;
  margin-bottom: 20px;
}
.filter-catalog__item {
  border-bottom: 1px solid #bbbbbb;
}
.filter-catalog__btn {
  width: 100%;
  transition: color 0.3s ease;
}
.filter-catalog__btn span {
  padding: 10px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 20px;
  flex-direction: row-reverse;
  transition: color 0.3s ease;
}
.filter-catalog__btn span::before {
  padding: 0 10px;
  font-size: 12px;
  transition: color 0.3s ease;
}
.filter-catalog__btn.active {
  color: #fc2db7;
}
.filter-catalog__btn.active span::before {
  transform: rotate(90deg);
}
@media (any-hover: hover) {
  .filter-catalog__btn:hover {
    color: #fc2db7;
  }
}
.filter-catalog__subsection {
  display: none;
}
.filter-catalog__subitem {
  background-color: #ffffff;
}
.filter-catalog__sublink {
  color: #654f5e;
  display: flex;
  justify-self: flex-start;
  padding: 5px 5px 5px 20px;
  transition: color 0.3s ease;
}
.filter-catalog__sublink:hover {
  color: #fc2db7;
}

.products-catalog__actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  background-color: #ebedee;
  padding: 25px;
}
.products-catalog__sort {
  display: flex;
  align-items: center;
}
.products-catalog__quantity {
  display: flex;
  align-items: center;
}
.products-catalog__items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  justify-content: center;
  gap: 30px;
}
.products-catalog__item {
  box-shadow: none;
  border: 1px solid #654f5e;
  border-radius: 10px;
}
.products-catalog__pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}

.sort-actions__label {
  margin-right: 10px;
}
.sort-actions .choices {
  width: 120px;
}

.quantity-actions__label {
  margin-right: 10px;
}
.quantity-actions .choices {
  width: 50px;
}

.catalog-pagination__arrow {
  height: 30px;
  flex: 0 0 30px;
  margin: 0 10px;
}
.catalog-pagination__number {
  font-size: 14px;
  height: 20px;
  flex: 0 0 20px;
  margin: 0 5px;
}

.choices.choices__list--dropdown {
  border-color: #ebedee;
}

.choices__list--dropdown .choices__item--selectable, .choices__list[aria-expanded] .choices__item--selectable {
  padding-right: 0;
}

.choices[data-type*=select-one].is-open::after {
  margin-top: 0;
  transform: rotate(-180deg);
}

.choices[data-type*=select-one]::after {
  border: none;
  top: 42%;
  background: url("../resources/icons/arrow-down.svg") center/cover no-repeat;
  height: 8px;
  width: 8px;
  fill: #654f5e;
  transition: transform 0.3s ease, margin-top 0.3s ease;
}

.choices__inner {
  margin: 0;
  padding: 0;
  padding-bottom: 0;
  align-items: flex-end;
  min-height: 30px;
  background-color: #ffffff;
}

.choices[data-type*=select-one] .choices__inner {
  padding-bottom: 0;
}

.choices__list--single {
  padding: 7px 7px 0 7px;
}

.choices__item {
  padding-right: 0;
  width: 100%;
  color: #654f5e;
  font-size: 14px;
  font-weight: 600;
  transition: background-color 0.3s ease;
}
.choices__item.is-highlighted {
  background-color: #ebedee;
}

.choices__list--dropdown .choices__list, .choices__list[aria-expanded] .choices__list {
  overflow: hidden;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted::after {
  display: none;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  background-color: rgba(21, 21, 21, 0.75);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.1s ease, visibility 0.1s ease;
}
.modal.active {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.modal__close {
  position: absolute;
  top: 30px;
  right: 30px;
  cursor: pointer;
  color: #ffffff;
  font-size: 26px;
  background-color: transparent;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .modal__close:hover {
    color: #fc2db7;
  }
}
.modal__content {
  border-radius: 10px;
  width: 360px;
  padding: 30px;
  background-color: #ffffff;
}
.modal__form_login, .modal__form_callback {
  display: none;
}
.modal__form_login.active, .modal__form_callback.active {
  display: block;
}

.form-modal__title {
  color: #654f5e;
  font-weight: 700;
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
}
.form-modal__content {
  width: 100%;
}
.form-modal__item {
  display: block;
}
.form-modal__item:not(:last-child) {
  margin-bottom: 15px;
}
.form-modal__input {
  width: 100%;
  height: 30px;
  outline: none;
  padding: 0 10px;
  color: #654f5e;
  font-size: 12px;
  background-color: #ebedee;
  border-radius: 10px;
  transition: box-shadow 0.3s ease 0s;
}
.form-modal__input:focus {
  box-shadow: 0 0 2px 2px rgba(252, 45, 183, 0.4);
}
.form-modal__name {
  display: block;
  padding-left: 10px;
  margin-bottom: 3px;
}
.form-modal__name span {
  color: #654f5e;
}
.form-modal__checkbox {
  cursor: pointer;
}
.form-modal__checkbox:checked + .checkbox-tag::after {
  top: 3px;
}
.form-modal__tag {
  font-size: 16px;
  line-height: 120%;
  cursor: pointer;
  padding-left: 25px;
}
.form-modal__tag::before {
  top: 0;
}
.form-modal__tag::after {
  top: 3px;
}
.form-modal__tag a {
  font-weight: 700;
  color: #654f5e;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .form-modal__tag a:hover {
    color: #fc2db7;
  }
}
.form-modal__btn {
  margin-top: 15px;
  flex: 0 0 40%;
  height: 35px;
  border-radius: 10px;
}
.form-modal__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}
.form-modal__forgot {
  margin-bottom: 15px;
  font-size: 14px;
  color: #654f5e;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .form-modal__forgot:hover {
    color: #fc2db7;
  }
}
.form-modal__btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer {
  padding: 80px 0;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../img/footer/footer.jpg") center/cover fixed no-repeat;
}
.footer__body {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 30px;
  margin-bottom: 70px;
}
.footer__credit {
  text-align: center;
  font-size: 20px;
  color: #ffffff;
}
.footer__credit a {
  color: #fc2db7;
  font-weight: 700;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .footer__credit a:hover {
    color: #a7265d;
  }
}

.footer-column__title {
  font-size: 20px;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 25px;
}
.footer-column__item:not(.footer-column__item:last-child) {
  margin-bottom: 20px;
}
.footer-column__link {
  color: #ffffff;
  transition: color 0.3s ease 0s;
  display: inline-flex;
  align-items: center;
  line-height: 120%;
}
.footer-column__link::before {
  padding-right: 10px;
  transition: padding-right 0.3s ease 0s;
}
@media (any-hover: hover) {
  .footer-column__link:hover {
    color: #fc2db7;
  }
  .footer-column__link:hover::before {
    padding-right: 20px;
  }
}
.footer-column__form {
  margin-bottom: 25px;
}
.footer-column__label {
  display: block;
  min-height: 35px;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 20px;
}
.footer-column__input {
  display: block;
  min-height: 35px;
  width: 100%;
  padding: 0 10px;
  border: 2px solid #ffffff;
  color: #ffffff;
  font-size: 14px;
  background: none;
  outline: none;
  border-radius: 20px;
}
.footer-column__input::placeholder {
  color: #ffffff;
}
.footer-column__input:focus {
  border-color: #fc2db7;
}
.footer-column__btn {
  height: 40px;
  padding: 5px 25px;
  background-color: #fc2db7;
}

.footer-social {
  display: flex;
  align-items: center;
}
.footer-social__item {
  height: 45px;
  flex: 0 0 45px;
}
.footer-social__item:not(.footer-social__item:last-child) {
  margin-right: 5px;
}
.footer-social__link {
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 24px;
  color: #654f5e;
  background-color: #ffffff;
  transition: background-color 0.3s ease, color 0.3s ease;
}
@media (any-hover: hover) {
  .footer-social__link:hover {
    background-color: #fc2db7;
    color: #ffffff;
  }
}